<template>
  <div class="spmsqadd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
            <table class="table table-bordered table-sm bg-light">
              <tr>
                <td class="form_title align-middle" colspan="4">評估日期</td>
                <td>
                  <v-text-field type="date" v-model="list.date" :rules="emptyRules"></v-text-field>
                </td>
              </tr>
              <!-- 入住適應暨心理社會評估 -->
              <tr>
                <td class="form_title" rowspan="12">
                  <span class="vertical-mode">簡易心智狀態 SPMSQ ( 錯誤請打X )</span>
                </td>
                <td class="form_title">1</td>
                <td colspan="2">今天是幾號? (年、月、日都對才算正確)</td>
                <td>
                  <select class="form-control" id="q1" name="q1" v-model="list.q1">
                    <option value="0">0</option>
                    <option value="1">X</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td class="form_title">2</td>
                <td colspan="2">今天是星期幾? (星期對才算正確)</td>
                <td>
                  <select class="form-control" id="q2" name="q2" v-model="list.q2">
                    <option value="0">0</option>
                    <option value="1">X</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td class="form_title">3</td>
                <td colspan="2">這是什麼地方? (對所在地有任何的描述都正確;說"我的家"或正確說出城鎮、醫院、機構的名稱都可以)</td>
                <td>
                  <select class="form-control" id="q3" name="q3" v-model="list.q3">
                    <option value="0">0</option>
                    <option value="1">X</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td class="form_title">4-1</td>
                <td
                  colspan="2"
                  class="text-nowrap"
                >您的電話號碼是幾號? (經確認號碼後証實無誤即算正確;或在會談時,能在二次間隔較長時間內重覆相同的號碼即算正確)</td>
                <td>
                  <select class="form-control" id="q4-1" name="q4-1" v-model="list['q4-1']">
                    <option value="0">0</option>
                    <option value="1">X</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td class="form_title">4-2</td>
                <td colspan="2">您住在什麼地方? (如長輩沒有電話才問此問題)</td>
                <td>
                  <select class="form-control" id="q4-2" name="q4-2" v-model="list['q4-2']">
                    <option value="0">0</option>
                    <option value="1">X</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td class="form_title">5</td>
                <td colspan="2">您幾歲了? (年齡與出生年月日符合才算正確)</td>
                <td>
                  <select class="form-control" id="q5" name="q5" v-model="list.q5">
                    <option value="0">0</option>
                    <option value="1">X</option>
                  </select>
                </td>
              </tr>

              <tr>
                <td class="form_title">6</td>
                <td colspan="2">您的出生年月日? (年、月、日都對才算正確)</td>
                <td>
                  <select class="form-control" id="q6" name="q6" v-model="list.q6">
                    <option value="0">0</option>
                    <option value="1">X</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td class="form_title">7</td>
                <td colspan="2">現任的總統是誰? (姓氏正確即可)</td>
                <td>
                  <select class="form-control" id="q7" name="q7" v-model="list.q7">
                    <option value="0">0</option>
                    <option value="1">X</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td class="form_title">8</td>
                <td colspan="2">前任的總統是誰? (姓氏正確即可)</td>
                <td>
                  <select class="form-control" id="q8" name="q8" v-model="list.q8">
                    <option value="0">0</option>
                    <option value="1">X</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td class="form_title" >9</td>
                <td colspan="2">您媽媽叫什麼名字? (不需要特別証實,只需長輩說出一個與他不同的女性姓名即可)</td>
                <td>
                  <select class="form-control" id="q9" name="q9" v-model="list.q9">
                    <option value="0">0</option>
                    <option value="1">X</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td class="form_title">10</td>
                <td colspan="2">從20減3開始算,一直減3減下去 (期間如有出現任何錯誤或無法繼續進行即算錯誤)</td>
                <td>
                  <select class="form-control" id="q10" name="q10" v-model="list.q10">
                    <option value="0">0</option>
                    <option value="1">X</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td></td>
                <td>
                  註1:進行方式:
                  <br />依上表所列的問題,詢問住民並將結果紀錄下來,(如果住民家中沒有電話,可將4-1題改為4-2題),答錯的問題請紀錄下來。
                  <br />註2:失智症狀評估標準:
                  <br />錯0~2題:心智功功能完整
                  <br />錯3~4題:輕度心智功能障礙
                  <br />錯5~7題:中度心智功能障礙
                  <br />錯8~10題:重度心智功能障礙
                </td>
                <td>錯幾題</td>
                <td>
                  <input
                    type="text"
                    class="form-control text-danger"
                    :value="total"
                    readonly
                  />
                </td>
              </tr>
            </table>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType=='add'" :loading="loading" :disabled="loading">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="processType=='edit'" :loading="loading" :disabled="loading">確認修改</v-btn>
        </v-card-actions>
        <!-- <pre>{{list}}</pre>, -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || "不可空白"],
      loading:false,
    };
  },
  computed:{
    total(){
      var totalPoint = 0;
      for (var i in this.list){
        if (i.includes('q') && this.list[i] == 1){
          console.log(i,this.list[i]);
          totalPoint ++;
        }
      }
      this.list.total = totalPoint;
      return totalPoint;

      // var t = parseInt(this.list.q1) 
      // +parseInt(this.list.q2)
      // +parseInt(this.list.q3)
      // +parseInt(this.list['q4-1'])
      // +parseInt(this.list['q4-2'])
      // +parseInt(this.list.q5)
      // +parseInt(this.list.q6)
      // +parseInt(this.list.q7)
      // +parseInt(this.list.q8)
      // +parseInt(this.list.q9)
      // +parseInt(this.list.q10);
      // this.list.total = t
      // return t
    },
  },

  props: ["viewData"],

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function(item) {
      this.dialog = true;
      this.editProcess(item);
    });
  },
  
  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";
      // this.list = {};

      //設定最後一筆資料為新增時的預設值及清除新增中不必要的值
      this.list = { ...this.viewData[0] };
      this.list.snkey = "";
      this.list.date = "";
      this.list.edit_man = "";
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //將頁面資料與父項的資料連結
      this.list = { ...item }; //複制一份實際修改資料內容
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list.u_snkey = this.$store.state.uData.snkey;
        this.list["create_man"] = this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        var postData = {
          u_snkey : this.list.u_snkey,
          datalist : JSON.stringify(this.list),
          create_man : this.list.create_man
        }

        this.loading = true;
        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/spmsq",
            qs.stringify(postData)
          )
          .then(rs => {
            console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "error" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list["edit_man"] = this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        var postData = {
          snkey : this.list.snkey,
          datalist : JSON.stringify(this.list),
          edit_man : this.list.edit_man
        }

        this.loading = true;
        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/spmsq",
            qs.stringify(postData)
          )
          .then(rs => {
            console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
              this.loading = false;
            }
          });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap/scss/bootstrap";
@import "node_modules/bootstrap-vue/src/index.scss";

.form_title {
  font-size: 1.2rem;
  text-align: center;
}
.vertical-mode {
  writing-mode: tb-rl;
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
}
</style>