<template>
  <div class="spmsqlist">
    <v-card class="mt-2">
      <v-card-title>
        <v-row>
          <v-col cols="12" md="8">
            <h2 class="subheading grey--text">
              <v-icon class="mx-1">fas fa-file-alt</v-icon>
              <span>
                {{this.$store.state.uData.p_code}} /
                {{this.$store.state.uData.name}}
              </span>
              <span>簡易心智狀態SPMSQ</span>
            </h2>
          </v-col>
          <v-col cols="12" md="4">
            <!-- 功能表 -->
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label="搜尋"
                v-model="serachKey"
                @keyup.stop="search()"
              ></v-text-field>

              <popupadd
                :viewData="viewData"
                @getAllData="getAllData"
                ref="editProcess"
                v-model="edititem"
              ></popupadd>
              <MultiPrintSelect :printChoice="printChoice" :printItems="printItems" @changeChoice="changeChoice"></MultiPrintSelect>
            </v-toolbar>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-card class="mt-2">
          <v-card-text>
            <!-- 表單內容 -->
            <v-data-iterator
              :items="viewData"
              :items-per-page.sync="itemsPerPage"
              :footer-props="{ itemsPerPageOptions }"
            >
              <template v-slot:default="props">
                <v-simple-table fixed-header class="mt-2 text-no-wrap">
                  <template v-slot:default>
                    <thead class="title">
                      <tr>
                        <th rowspan="3"></th>
                        <th rowspan="2">評估日期</th>
                        <th colspan="12">簡易心智狀態SPMSQ</th>
                        <th rowspan="2">紀錄人</th>
                        <th rowspan="2">修改紀錄</th>
                      </tr>
                      <tr>
                        <th>1.</th>
                        <th>2.</th>
                        <th>3.</th>
                        <th>4-1.</th>
                        <th>4-2.</th>
                        <th>5.</th>
                        <th>6.</th>
                        <th>7.</th>
                        <th>8.</th>
                        <th>9.</th>
                        <th>10.</th>
                        <th>錯幾題</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in props.items" :key="index">
                        <td class="d-flex align-center">
                          <v-checkbox label="列印選擇" v-model="item.check" v-if="!printChoice" @click.stop="filterPrintItem"></v-checkbox>
                          <v-menu transition="scale-transition" offset-y v-if="printChoice">
                            <template v-slot:activator="{ on }">
                              <v-btn color="primary" dark v-on="on">功能</v-btn>
                            </template>
                            <v-list>
                              <v-list-item @click="edit(item)">
                                <v-list-item-title>修改</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="del(item)">
                                <v-list-item-title>刪除</v-list-item-title>
                              </v-list-item>
                              <!-- <v-list-item @click.stop="print(item)">
                                <v-list-item-title>列印</v-list-item-title>
                              </v-list-item> -->
                            </v-list>
                          </v-menu>
                        </td>
                        <td>{{item.date}}</td>
                        <td>{{item.q1 | cc}}</td>
                        <td>{{item.q2 | cc}}</td>
                        <td>{{item.q3 | cc}}</td>
                        <td>{{item['q4-1'] | cc}}</td>
                        <td>{{item['q4-2'] | cc}}</td>
                        <td>{{item.q5 | cc}}</td>
                        <td>{{item.q6 | cc}}</td>
                        <td>{{item.q7 | cc}}</td>
                        <td>{{item.q8 | cc}}</td>
                        <td>{{item.q9 | cc}}</td>
                        <td>{{item.q10 | cc}}</td>
                        <td>{{item.total}}</td>
                        <td>{{item.create_man | filterName}}</td>
                        <td>{{item.edit_man | filterName}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </template>
            </v-data-iterator>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- <pre>{{viewData}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";
import MultiPrintSelect from "@/components/MultiPrintSelect";
import popupadd from "@/views/document/SpmsqAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd , MultiPrintSelect },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      viewData: [], //顯示的資料
      serachKey: "",
      edititem: {}, //修改中資料
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
      printChoice: true, //設定列印選擇
      printItems:[],
    };
  },

  mounted() {
    this.getAllData();
  },

  filters: {
    cc(value) {
      //表單中的圈跟叉判斷
      if (value == "0") {
        return "O";
      }
      if (value == "1") {
        return "X";
      }
    },
    filterName(val){
      if (val != undefined){
        var str = val.split('(');
        return str[0];
      }else{
        return '';
      }
    }
  },

  methods: {
    //切換顯示功能
    changeChoice(){
      this.printChoice = !this.printChoice;
      this.items.filter(i=>{
        i.check = "";
      })
      this.printItems = [];
    },
    //判斷選擇準備列印的內容
    filterPrintItem(){
      if (this.printItems.length == 5){
        var pop = { msg: "單次列印指定數最多5筆!!", type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
      }
      this.printItems = this.items.filter(i=>{
        return i.check;
      })
    },
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/spmsq",
          qs.stringify(postData)
        )
        .then(rs => {
          this.items = this.viewData = rs.data.map(i => {
            var newItem = JSON.parse(i.datalist);
            newItem.snkey = i.snkey;
            newItem.check = false;
            return newItem;
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
          this.sortBy();
        });
    },
    sortBy() {
      //依name排序
      this.viewData = this.viewData.sort(function(a, b) {
        return a.date < b.date ? 1 : -1;
      });
    },
    edit(item) {
      //修改功能
      this.$refs.editProcess.$emit("fromParent",item);
    },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then(res => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: "spmsq",
          info: JSON.stringify(delData)
        };

        if (res) {
          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/spmsq",
              qs.stringify(postData)
            )
            .then(rs => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
    search() {
      var searchArr = this.serachKey.split(" ");
      var outputitems = [...this.items];
      var str = "";
      searchArr.forEach(key => {
        if (key != "") {
          outputitems = outputitems.filter(item => {
            str = JSON.stringify(item).toUpperCase();
            if (str.includes(key.toUpperCase())) {
              return item;
            }
          });
        }
      });
      this.viewData = outputitems;
    },
    print(item) {
      // var printData = {
      //   medicalData: item,
      //   medicineData: medicineData
      // };

      //送出資料到列印頁面
      sessionStorage.setItem("printData", JSON.stringify(item));
      let routeData = this.$router.resolve({
        path: "/print/spmsqprint"
      });
      window.open(routeData.href, "_blank");
    }
  }
};
</script>


<style lang="scss">
.spmsqlist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>